.referrer {

  &__table {
    border-spacing: 0px;
    border-radius: 4px;
    border: 1px solid #e6eaee;
    background-color: #ffffff;
    margin-left: 30px;
    padding-top: 18px;
    width: 770px; }

  &__table-header {
    text-align: left; }

  &__table-name {
    font-size: 24px;
    color: #354052;
    padding-left: 30px;
    width: 260px; }

  &__table-header-column-views,
  &__table-header-column-sales,
  &__table-header-column-conversion,
  &__table-header-column-total {
    font-family: "Source Sans Pro Semibold";
    font-size: 14px;
    color: #7f8fa4;
    padding-right: 10px; }

  &__table-body {
    font-family: "Source Sans Pro Semibold";
    font-size: 14px;
    color: #333c48; }

  &__table-data {
    padding-left: 30px; }

  &__table-row {
    line-height: 34px; }

  &__table-row:nth-child(even) {
    background-color: #f5f8fa; } }
