.user-events {
  grid-area: user-events;
  background-color: #fff;
  width: 370px;
  height: 178px;
  padding: 25px 30px;
  margin-left: 10px;

  &__title {
    font-size: 12px;
    color: #818ea7;
    margin-bottom: 9px; }

  &__event-wrapper {
    display: flex;
    justify-content: space-between;
    padding: 0; }

  &__event-name {
    font-family: "Source Sans Pro Semibold";
    font-size: 16px;
    line-height: 16px;
    color: #56688a;
    margin-bottom: 9px;
    padding-top: 3px; }

  &__event-date {
    font-size: 14px;
    line-height: 14px;
    color: #56688a;
    margin-bottom: 12px; }

  &__event-photography,
  &__event-design {
    font-family: "Source Sans Pro Semibold";
    font-size: 10px;
    text-transform: uppercase;
    color: white;
    height: 18px;
    border-radius: 3px;
    padding: 3px 8px;
    line-height: 10px; }


  &__event-photography {
    background-color: #0474c8; }

  &__event-design {
    background-color: #37acc1; } }
